.nav {
 position: fixed;
 width: 90%;
 transition: all .5s ease-in-out;
 transform: translateX(-150%);
 top: 0;
 left: 0;
 z-index: 1;
 background: var(--light);

 &.visible {
  transform: none;
 }

 &.hide {
  display: none;
 }

 &.hiddenBtn {
  visibility: hidden;
 }

 svg {
  width: 26px;
  height: 24px;

  path {
   fill: var(--orange);
  }
 }

 .navBody {
  margin: 34px 0;
  padding: 16px;
  border-top: 1px solid var(--silv);

  ul {
   margin: 16px 0 0 10px;

   .dropdown {
    display: none;
   }
  }
 }
}

.burgerNav {
 position: relative;
 display: block;

 width: 30px;
 height: 20px;

 padding: 0;
 border: none;
 background: transparent;

 cursor: pointer;

 &::before,
 &::after {
  content: '';
  position: absolute;
  right: 0;

  width: 100%;
  height: 2px;

  background: var(--orange);
  transition: transform 0.5s ease-in;
 }

 &::before {
  top: 0;
 }

 &::after {
  bottom: 0;
 }

 .burgerLine {
  width: 100%;
  height: 2px;

  position: absolute;
  right: 0;
  top: 50%;

  background: var(--orange);
  transform: translateY(-50%);
  opacity: 1;
  transition: opacity 1s ease;
 }

 &.open {
  .burgerLine {
   opacity: 0;
  }

  &::before {
   top: 10px;
   transform: rotate(45deg);
  }

  &::after {
   top: 10px;
   transform: rotate(-45deg);
  }
 }
}