.errorPage {
  background: var(--light);
  min-height: 100vh;
  position: relative;
  overflow: hidden;

  .wrapper {
    padding: 0;
    margin: 0;
  }

  .errorPageBody {
    width: 100%;
    position: absolute;
    top: 40%;
    text-align: center;
    color: var(--orange);
  }

  .typeErr {
    font-size: 56px;
    font-weight: 700;
  }

  .textErr {
    font-size: 24px;
  }
}