@import 'src/styles/_mixins.scss';

.btnContacts {
 margin: 51px 0 0 10px;
 @include gridGap;

 a:not(:first-child) {
  margin: 0 0 0 10px;
 }

 .contacts {
  @include flexble;

  b {
   display: inline-block;
   width: 55px;
  }
 }
}

.btnControls {
 display: flex;
 align-items: center;

 a:not(:first-child) {
  margin: 0 0 0 10px;
 }

 .btnLikes {
  display: block;
 }
}

.backBtn,
.closeBtn {
 position: absolute;
 cursor: pointer;
 top: 5px;
 font-size: 24px;
 z-index: 9;

 &:not(.backBtn) {
  right: 16px;
 }

 &:not(.closeBtn) {
  left: 16px;
 }
}

.catalogBtn {
 background: var(--orange);
 color: var(--light);
 text-align: center;
 margin: 0 auto;

 padding: 5px 10px;
 border-radius: 6px;
 width: 90%;
 cursor: pointer;
}

@media screen and (min-width: 1440px) {
 .dataPhone {
  display: flex;
  font-size: 12px;
  font-weight: 500;

  p:not(:first-child) {
   margin: 0 0 0 24px;
  }
 }
}