@import 'src/styles/mixins';

#header {
 .headerTop {
  display: none;
 }

 .headerBody {
  @include flexble;
  padding: 16px 0;
  border-bottom: 1px solid var(--silv);
 }

 .searchForm {
  display: none;
 }

 .tNav {
  > a {
   display: none;
  }

  ul {
   @include gridGap;

   .navLink {
    color: var(--link);
    transition: all 0.5s ease-in-out;

    &:hover {
     color: var(--orange);
    }
   }

   &.dropdown {
    display: none;
   }
  }
 }

 .headerLogo {
  img {
   width: 32px;
  }

  .catalogBtn, .dataWorked {
   display: none;
  }
 }
}

@media screen and (min-width: 1440px) {
 #header {
  border-bottom: 1px solid var(--silv);

  .headerTop {
   display: block;
   background: var(--light-silv);

   .headerTopBody {
    @include flexble;
   }

   .btnContacts {
    margin: 0;

    > .contacts {
     display: none;
    }
   }

   .nav {
    width: auto;
    display: block;
    transform: none;
    position: static;
    background: transparent;
   }

   .closeBtn {
    display: none;
   }

   .catalogBtn {
    display: none;
   }

   .navBody {
    margin: 0;
    padding: 14px 0;

    ul {
     @include flexCenter;
     margin: 0;
     font-size: 12px;

     li {
      color: var(--link);
      position: relative;
      transition: all .5s ease-in-out;

      &:not(:first-child) {
       margin: 0 0 0 24px;
      }

      &:hover {
       color: var(--orange);

       > .dropdown {
        @include gridGap;
        visibility: visible;
        opacity: 1;
        min-width: 200px;
        text-align: left;
        box-shadow: 0 3px 5px -1px #ccc;
        transition: all .5s ease-in-out;
       }
      }

      .dropdown {
       transition: all .5s ease-in-out;
       visibility: hidden;
       opacity: 0;
       position: absolute;
       left: 0;
       top: 15px;
       display: none;
       background: var(--light);
       padding: 24px;
       z-index: 3;

       &:hover {
        visibility: visible;
        opacity: 1;
        @include gridGap;
        min-width: 200px;
        text-align: left;
        box-shadow: 0 3px 5px -1px var(--light-silv);
        transition: all .5s ease-in-out;
       }

       li {
        clear: both;
        width: 100%;
        text-align: left;
        margin: 0;
        border-style: none;
       }
      }
     }

     .btnControls, {
      display: none;
     }
    }

    .btnContacts {
     display: none;
    }
   }
  }
 }
}