.catalogBlock {
 width: 80%;
 position: absolute;
 top: 0;
 left: 0;
 z-index: 1;
 transition: all .5s ease-in-out;
 transform: translateX(-100%);
 background: var(--light);
 padding: 16px 16px 0;

 &.visible {
  transform: none;
 }

 &.hide {
  display: none;
 }

 .catalogTitle {
  .bg {
   width: 100%;
   height: 34px;
   position: absolute;
   top: 0;
   left: 0;
   background: var(--orange);
  }

  .closeBtn,
  .backBtn {
   color: var(--light);
  }

  h3 {
   position: absolute;
   top: 8px;
   left: 50%;
   transform: translateX(-50%);
   color: var(--light);
   font-weight: 500;
   font-size: 14px;
  }
 }
}